import React, {useEffect} from 'react';
import './App.css';
import firebase from './firebase';
import Chart from "chart.js";

const App = () => {
  let playerStats, seasonStats, breakEvensSource, totalPointsSource;
  let breakEvenFilter = ["CTR", "HLF", "EDG", "HOK", "MID", "WFB"];
  let totalPointsFilter = ["CTR", "HLF", "EDG", "HOK", "MID", "WFB"];
  let userType = "";
  useEffect(() => { 
    startUp();
  });

  async function startUp() {
    console.log("starting...");
    //console.log("screen.width:" + window.innerWidth);
    document.getElementById("web-content").style.display = "none";
    if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
      userType = "Mobile";
    } else {
      userType = "Computer";
    }
    //userType = "Mobile";
    playerStats = await firebase.getPlayers();
    seasonStats = await firebase.getSeason();
    breakEvensSource = await firebase.getBreakEvens();
    totalPointsSource = await firebase.getTotalPoints();
    for (let i = 0; i < document.getElementsByClassName("checkFilt").length; i++) {
      document.getElementsByClassName("checkFilt")[i].checked = true;
    }
    document.getElementsByClassName("loadingData")[0].style.display = "none";
    getSelect();
    getHtml();
    let breakEvens = [...breakEvensSource];
    getBreakEvensHtml(breakEvens);
    let totalPoints = [...totalPointsSource];
    getTotalHtml(totalPoints);
    //getCharts();
    document.getElementById("web-content").style.display = "block";
  }

  function getCharts(playerName) {
    //document.getElementById("FantasyScoresID").style.width = "400px";
    if (userType === "Mobile") {
      for (let i = 0; i < document.getElementsByClassName("canvasChart").length; i += 1) {
        document.getElementsByClassName("canvasChart")[i].style.width = "100%";
        document.getElementsByClassName("canvasChart")[i].style.display = "block";
        document.getElementsByClassName("canvasChart")[i].style.marginLeft = "0";
      }
    }
    let data = [];
    for (let p = 0; p < seasonStats.length; p++) {
      if (seasonStats[p].player === playerName) {
        data.push(seasonStats[p]);
      }
    }
    //console.log(data);
    let roundLables = [];
    let fantasyScores = [];
    let colours = [];
    let borderColours = [];
    let borderPriceColours = [];
    let priceChanges = [];
    for (let i = 0; i < data.length; i++) {
      roundLables.push(parseInt(data[i].rnd));
      fantasyScores.push(parseInt(data[i].fantasy_points));
      colours.push('rgba(255, 0, 0, 0.5)');
      borderColours.push('rgba(255, 0, 0, 1)');
      borderPriceColours.push('rgba(0, 130, 0, 1)');
      priceChanges.push(parseInt(data[i].price_changes.split("k")[0].replace("$", "")))
    }
    let ctx = document.getElementById("FantasyScoresID").getContext('2d');
    new Chart(ctx, {
        type: 'bar',
        data: {
            labels: roundLables,
            datasets: [{
                data: fantasyScores,
                backgroundColor: colours,
                borderColor: borderColours,
                borderWidth: 1
            }]
        },
        options: {
          responsive: true,
          legend: {
            display: false
          },
          scales: {
            yAxes: [{
                ticks: {
                    beginAtZero: true
                }
            }]
          }
        }          
    });

    ctx = document.getElementById("PriceChangesID").getContext('2d');
    new Chart(ctx, {
      type: 'line',
      data: {
          labels: roundLables,
          datasets: [{
              data: priceChanges,
              fill: false,
              lineTension: 0,
              borderColor: borderPriceColours,
              borderWidth: 1
          }]
      },
      options: {
        responsive: true,
        legend: {
          display: false
        },
        scales: {
          yAxes: [{
              ticks: {
                  suggestedMin: Math.min.apply(null, priceChanges) - 20,
                  suggestedMax: Math.min.apply(null, priceChanges) + 20
              }
          }]
        } 
      }        
    });

  }
  
  function getHtml() {
    let myHtml = "";
    //console.log(playerStats);
    for (let p = 0; p < playerStats.length; p++) {
      let name = playerStats[p].player;
      let lastName = name.split(" ")[1];
      let firstName = name.split(" ")[0];
      let x = lastName + ", " + firstName;
      if (x === document.getElementById('playerSelectID').value) {
        let teamClass = playerStats[p].team.split(" ");        
        teamClass = teamClass[teamClass.length - 1] + "Class";
        let imgLink = playerStats[p].photo_link;
        if (!imgLink.includes("rugbyimages")) {
          imgLink = "https://favim.com/assets/img/avatar-default.jpg";
        }

        myHtml += "<div class='playerProfile" + userType + "'>";
        myHtml += "<div class='playerProfileDetails" + userType + "'>";
        myHtml += "<h3>" + playerStats[p].player + "</h3>";
        myHtml += "<p>" + playerStats[p].team + "</p>";
        myHtml += "<img src='" + imgLink + "' />";
        myHtml += "</div>";
        myHtml += "<div class='playerProfileTable" + userType + "'>";
        if (playerStats[p].opponent_avg !== "nan") {
          myHtml += "<p><b>Next Opponent:</b> " + playerStats[p].next_opponent + " (averages " + playerStats[p].opponent_avg + ")</p>";
        } else {
          myHtml += "<p><b>Next Opponent:</b> " + playerStats[p].next_opponent +"</p>";
        }   
        if (userType === "Computer") {
          myHtml += "<table class='" + teamClass + "'><tbody><tr><th>Price</th><th>Position</th><th>Season Avg</th><th>Break Even</th><th>Ownerage</th><th>PPM</th></tr>";
          myHtml += "<tr><td>$" + playerStats[p].price + "k</td><td>" + playerStats[p].position + "</td><td>" + playerStats[p].season_avg + "</td><td>" + playerStats[p].break_even + "</td><td>" + playerStats[p].ownerage + "</td><td>" + playerStats[p].ppm + "</td></tr>";
          myHtml += "</tbody></table></div>";
          myHtml += "</div>";
          myHtml += "<br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br /><br />";
        } else {
          myHtml += "<table class='" + teamClass + "'><tbody>";
          myHtml += "<tr><th>Price</th><td>$" + playerStats[p].price + "k</td></tr>";
          myHtml += "<tr><th>Position</th><td>" + playerStats[p].position + "</td></tr>";
          myHtml += "<tr><th>Season Avg</th><td>" + playerStats[p].season_avg + "</td></tr>";
          myHtml += "<tr><th>Break Even</th><td>" + playerStats[p].break_even + "</td></tr>";
          myHtml += "<tr><th>Ownerage</th><td>" + playerStats[p].ownerage + "</td></tr>";
          myHtml += "<tr><th>PPM</th><td>" + playerStats[p].ppm + "</td></tr>";
          myHtml += "</tbody></table></div>";
          myHtml += "</div>";
          myHtml += "<br />";
        }
                    
        //myHtml += `<div class="canvasClass"><canvas id="CanvasID" width="100" height="100"></canvas></div>`;
        getCharts(playerStats[p].player);
      } 
    }

    document.getElementById("myHtml").innerHTML = myHtml;
  }

  function getBreakEvensHtml(breakEvens) {
    //breakEvens.sort((a,b) => (parseInt(a.break_even) > parseInt(b.break_even)) ? 1 : ((parseInt(b.break_even) > parseInt(a.break_even)) ? -1 : 0))
    let beHtml = "";
    let maxPlayerList = breakEvens.length;    
    for (let i = 0; i < maxPlayerList; i++) {
      if (i === 0) {
        beHtml += "<img src='" + breakEvens[i].photo_link + "' />";
        beHtml += "<div class='tableHeader'><table><tbody><tr><th>Player</th><th>Break Even</th><th>Price</th><th>Position (Fantasy)</th><th>Avg (Season)</th><th>Team</th><th>Position (next round)</th></tr></tbody></table></div>";
        beHtml += "<div class='tableContent'><table><tbody>";
      }      
      beHtml += "<tr><td>" + breakEvens[i].player + "</td><td>" + breakEvens[i].break_even + "</td><td>$" + breakEvens[i].price + "k</td><td>" + breakEvens[i].fantasy_position + "</td><td>" + breakEvens[i].season_avg + "</td><td>" + breakEvens[i].team + "</td><td>" + breakEvens[i].position_round + "</td></tr>";

    }
    beHtml += "</tbody></table></div>";
    document.getElementById("lowestBreakEvens").innerHTML = beHtml;
  }

  function getTotalHtml(totalPoints) {
    //breakEvens.sort((a,b) => (parseInt(a.break_even) > parseInt(b.break_even)) ? 1 : ((parseInt(b.break_even) > parseInt(a.break_even)) ? -1 : 0))
    let totHtml = "";
    let maxPlayerList = totalPoints.length;    
    for (let i = 0; i < maxPlayerList; i++) {
      if (i === 0) {
        totHtml += "<img src='" + totalPoints[i].photo_link + "' />";
        totHtml += "<div class='tableHeader'><table><tbody><tr><th>Player</th><th>Total Points</th><th>Price</th><th>Position</th><th>Team</th></tr></tbody></table></div>";
        totHtml += "<div class='tableContent'><table><tbody>";
      }      
      totHtml += "<tr><td>" + totalPoints[i].player + "</td><td>" + totalPoints[i].total_points + "</td><td>$" + totalPoints[i].price + "k</td><td>" + totalPoints[i].fantasy_position + "</td><td>" + totalPoints[i].team + "</td></tr>";

    }
    totHtml += "</tbody></table></div>";
    document.getElementById("totalPoints").innerHTML = totHtml;
  }

  function getSelect() {
    let selectHTML = "<select id='playerSelectID'>";
    for (let p = 0; p < playerStats.length; p++) {
      let name = playerStats[p].player;
      let lastName = name.split(" ")[1];
      let firstName = name.split(" ")[0];
      let x = lastName + ", " + firstName;
      selectHTML += "<option value='" + x + "'>" + x + "</option>";
    }
    selectHTML += "</select>";
    document.getElementById("playerSearchID").innerHTML = selectHTML;
  }

  function updateBreakEvens(pos) {
    console.log("udating filter");
    if (breakEvenFilter.includes(pos)) {
      let index = breakEvenFilter.indexOf(pos);
      if (index !== -1) {
        breakEvenFilter.splice(index, 1);
      }
    } else {
      breakEvenFilter.push(pos);
    }
    console.log(breakEvenFilter);
    let breakEvens = [...breakEvensSource];
    for (let i = 0; i < breakEvens.length; i++) {
      let pos = breakEvens[i].fantasy_position;
      let positions = pos.split("/");      
      let passed = false;
      for (let j = 0; j < positions.length; j++) {
        if (breakEvenFilter.includes(positions[j])) {
          passed = true;
        }
      }
      if (passed === false) {
        breakEvens.splice(i, 1);
        i -= 1;
      }
    }
    getBreakEvensHtml(breakEvens);

  }

  function updateTotalPoints(pos) {
    console.log("udating filter");
    if (totalPointsFilter.includes(pos)) {
      let index = totalPointsFilter.indexOf(pos);
      if (index !== -1) {
        totalPointsFilter.splice(index, 1);
      }
    } else {
      totalPointsFilter.push(pos);
    }
    console.log(totalPointsFilter);
    let totPoints = [...totalPointsSource];
    for (let i = 0; i < totPoints.length; i++) {
      let pos = totPoints[i].fantasy_position;
      let positions = pos.split("/");      
      let passed = false;
      for (let j = 0; j < positions.length; j++) {
        if (totalPointsFilter.includes(positions[j])) {
          passed = true;
        }
      }
      if (passed === false) {
        totPoints.splice(i, 1);
        i -= 1;
      }
    }
    getTotalHtml(totPoints);

  }

  return (
    <div className="App">
      <div className="loadingData">
        <br /><br />
        Getting data from database...
        <br /><br />
        <div className="spinner loading"></div>
      </div>      
      <br />
      <div id="web-content">
        <div id="playerSearchID"></div>
        <button onClick={getHtml}>Search Player</button>
        <div id="myHtml"></div>
        
        <div className="canvases">
          <div className="canvasChart">
            <h4>Scores this season:</h4>
            <div className="canvasClass"><canvas id="FantasyScoresID" width="100" height="100"></canvas></div>
          </div>
          <div className="canvasChart" style={{marginLeft:"20px"}}>
            <h4>Price changes ($):</h4>
            <div className="canvasClass"><canvas id="PriceChangesID" width="100" height="100"></canvas></div>
          </div>
        </div>
        <br />
        <hr />

        <div className="topPlayers">
          <h3>Lowest Break Evens</h3>
          <b>Position filter:</b>
          <br />
          <div className="filter"><label>CTR<input className="checkFilt" type="checkbox" defaultChecked onChange={(e) => updateBreakEvens("CTR", e)} /></label></div>
          <div className="filter"><label>HLF<input className="checkFilt" type="checkbox" defaultChecked onChange={(e) => updateBreakEvens("HLF", e)} /></label></div>
          <div className="filter"><label>EDG<input className="checkFilt" type="checkbox" defaultChecked onChange={(e) => updateBreakEvens("EDG", e)} /></label></div>
          <div className="filter"><label>HOK<input className="checkFilt" type="checkbox" defaultChecked onChange={(e) => updateBreakEvens("HOK", e)} /></label></div>
          <div className="filter"><label>MID<input className="checkFilt" type="checkbox" defaultChecked onChange={(e) => updateBreakEvens("MID", e)} /></label></div>
          <div className="filter"><label>WFB<input className="checkFilt" type="checkbox" defaultChecked onChange={(e) => updateBreakEvens("WFB", e)} /></label></div>
          <div id="lowestBreakEvens"></div>

          <br />
          <hr />
          <h3>Highest Total Points</h3>
          <b>Position filter:</b>
          <br />
          <div className="filter"><label>CTR<input className="checkFilt" type="checkbox" defaultChecked onChange={(e) => updateTotalPoints("CTR", e)} /></label></div>
          <div className="filter"><label>HLF<input className="checkFilt" type="checkbox" defaultChecked onChange={(e) => updateTotalPoints("HLF", e)} /></label></div>
          <div className="filter"><label>EDG<input className="checkFilt" type="checkbox" defaultChecked onChange={(e) => updateTotalPoints("EDG", e)} /></label></div>
          <div className="filter"><label>HOK<input className="checkFilt" type="checkbox" defaultChecked onChange={(e) => updateTotalPoints("HOK", e)} /></label></div>
          <div className="filter"><label>MID<input className="checkFilt" type="checkbox" defaultChecked onChange={(e) => updateTotalPoints("MID", e)} /></label></div>
          <div className="filter"><label>WFB<input className="checkFilt" type="checkbox" defaultChecked onChange={(e) => updateTotalPoints("WFB", e)} /></label></div>
          <div id="totalPoints"></div>
        </div>
      </div>
      <br /><br />
      
      
    </div>
  );
}

export default App;
